.columns {
  display: flex;
  justify-content: center;
  flex-wrap: wrap;
  /* margin-top: 20px; */
}

.column {
  background-color: #18181a;
  /* flex: 1; */
  flex-direction: column;
  width: '100%';
  justify-content: top;
  padding-left: 10px;
  margin-right: 1%;
  margin-left: 1%;
  margin-top: 1%;
  font-size: 16px;
  /* height: '100%'; */
  /* background-color: #ccc; */
  /* display: flex; */
  /* align-items: center; */
  /* border-radius: 8px; */
  transition: background-color 1000ms ease;
}

.columnDivider {
  border-image: linear-gradient(to right, rgb(255, 255, 255), rgb(255, 255, 255)) 1;
  transition: 250ms ease;
  width: 80%;
  margin-right: 100%
}

.column:hover {
  background-color: rgb(58, 58, 58);
}
/* 
.column:hover > .columnDivider {
  border-image: linear-gradient(to right, rgb(200, 0, 0), rgb(0, 0, 200)) 1;
} */
@media screen and (min-width: 400px) {
  .column {
    width: 30%;
    margin-right: 2.5px; 
    margin-left: 2.5px; 
  }
}
.column p {
  font-size: clamp(14px, 2vmin, 16px);
  color: white;
  overflow: hidden; 
  text-overflow: ellipsis; 
  white-space: normal; 
}
.column h1 {
  font-size: clamp(16px,calc(10px + 2vmin),24px);
  font-weight: 100;
  font-family: PlayfairDisplay;
  color: white;
  overflow: hidden; 
  text-overflow: ellipsis; 
  text-align: left;
  white-space: normal; 
}
